.register{
    background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(https://www.teahub.io/photos/full/60-604322_travel-background-wallpaper-hd.jpg); 
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    padding-bottom: 20px;
    color: skyblue;
}
form{
    width: 50%;
    margin: 10px auto;
}
.mobile{
display: none;
}
.desk{
display: inline-block;
}
@media only screen and (max-width: 700px){
    .desk{
        display: none;
    }
    .mobile{
        display: inline-block;
    }
}